import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import PartnerStyles from './Partner.style';
import Slider from 'react-slick';

import PartnerImg1 from './../../assets/images/p1.png';
import PartnerImg2 from './../../assets/images/p2.png';
import PartnerImg3 from './../../assets/images/p3.png';
import PartnerImg4 from './../../assets/images/p4.png';
import PartnerImgGemini from './../../assets/images/geminisoft.webp';
import { color } from './../../utilities/constants';

var minRadius = 80;
var colorArray = [
    color.primary,
    color.primary_o,
    color.light_o,
    color.black_o,
]

const Partner = (props) => {
    const {
        theme
    } = props;
    const [size, setSize] = useState([0, 0]);

    const containerCanvas = useRef(null);

    const settingSlider = {
        infinite: false,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 1,
        arrows: false,
        autoplaySpeed: 4000,
        dots: true,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1,
                    infinite: true,
                    dots: true
                }
            },
            {
                breakpoint: 560,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    infinite: true,
                    dots: true
                }
            },
        ]
    }

    useLayoutEffect(() => {
        function updateSize() {
            let bounding = containerCanvas.current.getBoundingClientRect();
            setSize([bounding.width, bounding.height]);
        }
        window.addEventListener('resize', updateSize);
        updateSize();
        return () => window.removeEventListener('resize', updateSize);
    }, []);

    useEffect(() => {
        var canvas = document.querySelector('#background-animation');
        const maxWidth = size[0];
        const maxHeight = size[1];

        canvas.width = maxWidth;
        canvas.height = maxHeight;

        var gl = canvas.getContext('2d');

        function Circle(x, y, dx, dy, radius, color) {
            this.x = x;
            this.y = y;
            this.dx = dx;
            this.dy = dy;
            this.radius = radius;
            this.minRadius = radius;
            this.color = color;
            this.draw = () => {
                gl.beginPath();
                gl.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
                // gl.strokeStyle = this.color;
                // gl.stroke();
                gl.fillStyle = this.color;
                gl.fill();
            }
            this.update = () => {
                if (this.x + this.radius > maxWidth || this.x - this.radius < 0) {
                    this.dx = -this.dx;
                }

                if (this.y + this.radius > maxHeight || this.y - this.radius < 0) {
                    this.dy = -this.dy;
                }

                this.x += this.dx;
                this.y += this.dy;

                this.draw();
            }
        }

        var circleArray = [];

        function animate() {
            requestAnimationFrame(animate);
            gl.clearRect(0, 0, maxWidth, maxHeight); // clear toàn bộ nội dung trong khung canvas, clear các hình trước đó.

            for (let index = 0; index < circleArray.length; index++) {
                circleArray[index].update();
            }
        }

        function init() {
            circleArray = []; // reset
            for (let index = 0; index < 10; index++) {
                var radius = Math.floor(Math.random() * 4 + minRadius);
                var x = Math.random() * (maxWidth - radius * 2) + radius;
                var y = Math.random() * (maxHeight - radius * 2) + radius;
                var dx = (Math.random() - 0.5) + 1;
                var dy = (Math.random() - 0.5) + 1;
                // var color = '#' + (0x1000000 + (Math.random()) * 0xffffff).toString(16).substr(1, 6);
                var color = colorArray[Math.floor(Math.random() * colorArray.length)];
                circleArray.push(new Circle(x, y, dx, dy, radius, color));
            }
        }

        init();
        animate();
    }, [size])

    return (
        <PartnerStyles className='d-flex align-items-center justify-content-center pos-relative'>
            <div className="wrap-background-fixed" ref={containerCanvas}>
                <canvas id='background-animation'></canvas>
            </div>

            <div className="container pos-relative">
                <h1 className="text-center">Đơn vị hỗ trợ và hợp tác</h1>
                <div className="pos-relative">
                    <Slider {...settingSlider}>
                        <div className="card">
                            <div className="wrap-img">
                                <img src={PartnerImgGemini} alt="" />
                            </div>
                        </div>
                        <div className="card">
                            <div className="wrap-img">
                                <img src={PartnerImg1} alt="" />
                            </div>
                        </div>
                        <div className="card">
                            <div className="wrap-img">
                                <img src={PartnerImg2} alt="" />
                            </div>
                        </div>
                        <div className="card">
                            <div className="wrap-img">
                                <img src={PartnerImg3} alt="" />
                            </div>
                        </div>
                        <div className="card">
                            <div className="wrap-img">
                                <img src={PartnerImg4} alt="" />
                            </div>
                        </div>
                    </Slider></div>
            </div>
        </PartnerStyles>
    )
}

export default Partner;
