import styled from 'styled-components';

export default styled.div`
height: 100vh;
overflow: hidden;
border-left: 1px solid ${props => props.theme.border};
background-color: ${props => props.theme.bgColor_2};
.wrap-background-fixed{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: .1;
}
.container{
    padding: 0 20px;
    h1{
        font-size: 30px;
        font-weight: 400;
        padding: 5px;
    }
    .slick-slider{
        *:focus {
            outline: 0;
            outline: none;
        }
        .slick-track{
            display: flex;
            align-items: center;
        }
        .card{
            display: flex;
            align-items: center;
            .wrap-img{
                padding: 10px;
                img{
                    width: 100%;
                }
            }
        }
    }
    .grid-container{
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 50px;
        
    }
    @media(max-width: 769px){
        h1{
            font-size: 20px;
            margin-bottom: 20px;
        }
        .grid-container{
            grid-template-columns: 1fr 1fr;
            padding: 0 10px;
        }
    }
}
`;